import { Component } from '@angular/core';
import {
    trigger,
    state,
    style,
    animate,
    transition
  } from '@angular/animations';

  import { AuthService } from './auth.service';
  import { RegService } from './reg.service';
  import { User } from './user';

@Component({
    selector: 'auth-comp',
    templateUrl: './auth.html',
    styleUrls: ['./auth.css'],
    providers: [
        AuthService,
        RegService
    ],
    animations: [
        trigger('selectItemActive', [
            state('active, void', style({
                display: 'block',
                opacity: '1',
            })),
            state('inactive',   style({
                display: 'none',
                opacity: '0',
            })),
            transition('inactive => active', [ animate('0.2s 200ms ease-in-out', style({ opacity: '1' })) ]),
            transition('active => inactive', [ animate('0.2s ease-in-out', style({ opacity: '0' })) ]),
        ]),
        trigger('selectItem', [
            state('active, void', style({
                display: 'none',
                opacity: '0',
            })),
            state('inaction',   style({
                display: 'block',
                opacity: '1',
            })),
            transition('inactive => active', [ animate('0.2s ease-in-out', style({ opacity: '0' })) ]),
            transition('active => inactive', [ animate('0.2s 200ms ease-in-out', style({ opacity: '1' })) ]),
        ]),
    ]
})
export class AuthComponent {
    public selectState = 'inactive';
    emailInpt:string;
    passInpt:string;
    formBtnClick() {
        console.log(this.emailInpt);
        console.log(this.passInpt);
    }
    formSelectorBtnClick() {
        this.selectState = this.selectState === 'active' ? 'inactive' : 'active';
    }

    user: User = new User(); // данные вводимого пользователя
    userReg: User = new User();

    receivedUser: User; // полученный пользователь
    receiveduserReg: User;

    done: boolean = false;
    constructor(private authService: AuthService, private regService: RegService){}

    submitAuth(user: User){
        this.authService.postData(user)
            .subscribe(
                (data: User) => {this.receivedUser=data; this.done=true; console.log(data)},
                error => console.log(error)
        );
    }
    submitReg(userReg: User){
        this.regService.postData(userReg)
            .subscribe(
                (data: User) => {this.receiveduserReg=data; this.done=true; console.log(data)},
                error => console.log(error)
        );
    }
}



