import { Component, OnInit } from '@angular/core';
import { GameService } from './game.service';
import { SocketService } from './socket.service';


@Component({
  selector: 'game-comp',
  templateUrl: './game.html',
  styleUrls: ['./game.css'],
  providers: [ 
    GameService,
    SocketService
  ]
})
export class GameComponent implements OnInit{
  data:string;
  randDeck;
  rand;
  arrTemp = [];
  constructor(private gameService: GameService, private socket: SocketService){
    this.socket.emit('event', this.data).subscribe(
      (data) => {
          console.log('Success',data);
      },
      (error) => {
          console.log('Error',error);
      },
      () => {
          console.log('complete');
      }
  );
  }
  ngOnInit(){
    this.gameService.getData().subscribe((data) => {
      for (var i = 0; i < 3; i++) {
        this.randDeck = Math.floor(Math.random() * (4 - 0)) + 0;
        if (this.randDeck == 0) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck1[this.rand]);
          arrTemp1.push('deck1');
          this.arrTemp.push(arrTemp1);

        } else if (this.randDeck == 1) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck2[this.rand]);
          arrTemp1.push('deck2');
          this.arrTemp.push(arrTemp1);

        } else if (this.randDeck == 2) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck3[this.rand]);
          arrTemp1.push('deck3');
          this.arrTemp.push(arrTemp1);

        } else if (this.randDeck == 3) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck4[this.rand]);
          arrTemp1.push('deck4');
          this.arrTemp.push(arrTemp1);
        }
      }
    });
    console.log(this.arrTemp);
  }
}


